.card{
    @apply
    p-4
    w-80
}

.summary{
    @apply
    text-center
    float-right
    justify-center
}

.cardLink{
    @apply
    block
    shadow-sm
    hover:shadow-md
    border-zinc-900/10
    shadow-zinc-900/10
    rounded-md
    overflow-hidden
    h-full
    m-2
}
.favButton{
    @apply
    flex
    h-6
    w-6
    m-2
    ml-auto
    hover:text-red-500
    text-zinc-900/80
}
.cardHeader{
    @apply
    relative
    pb-64
    overflow-hidden
    shadow-sm
    w-full
}
.cardImg{
    @apply
    absolute
    inset-0
    max-h-56
    w-full
    object-contain
    my-4
    px-4
    
}
.cardHead{
    transition: transform .5s ease--in-out;
}
.cardLink:hover{
    transform: scale(1.02);
}
.cardBody{
    @apply
    p-4
    h-full
    flex
    flex-1
    flex-col
    text-sm
    text-zinc-900/80
}

.cardBody .brand .cardTitle{
    @apply
    mt-1
    mb-2
}

.brand{
    @apply
    font-bold
    hover:text-blue-700
}

.cardTitle{
    @apply
    font-light
    truncate
}
.rating{
    @apply
    flex
    flex-row
    mb-2
}
.rating .starIcon, .emptyStarIcon{
    @apply
    h-4
    w-4
    my-auto
}
.starIcon{
    @apply
    text-yellow-400
}
.emtpyStarIcon{
    @apply
    text-zinc-900/80
}

.addToCart{
    @apply
    mx-auto
    text-center
    w-full
    mt-2
}

.addToCartButton{
    @apply
    inline-flex
    justify-center
    w-full
    rounded-md
    shadow-sm
    text-sm
    px-2
    bg-red-600
    py-2
}

.shoppingCartIcon{
    @apply
    my-auto
    h-5
    w-5
}

.buttonText{
    @apply
    font-bold
    ml-4
    text-zinc-900/80
}

