.image{
    @apply
    lg:w-1/3
    inset-0
    max-h-max
    w-full
    py-4
    my-auto
    px-4
    object-scale-down
    shadow-lg
    border-2
    border-zinc-900/10
    shadow-zinc-900/10
    rounded-md

}

.rating{
    @apply
    flex
    flex-row
    mb-2
}

.rating .starIcon, .emptyStarIcon{
    @apply
    h-4
    w-4
    my-auto
}

.starIcon {
    @apply
    text-yellow-400
}

.emptyStarIcon{
    @apply
    text-zinc-900/80
}

.addToCart{
    @apply
    mx-auto
    text-center
    w-full
    mt-2
    animate-bounce
}

.addToCartButton{
    @apply
    inline-flex
    justify-center
    w-full
    rounded-md
    shadow-sm
    text-sm
    px-2
    bg-blue-600
    py-2
    
    hover:bg-yellow-500

}

.shoppingCartIcon{
    @apply
    my-auto
    h-5
    w-5
}

.buttonText{
    @apply
    font-bold
    text-zinc-100/80
}

.favButton{
    @apply
    h-6
    w-6
    m-2
    ml-auto
    flex
    hover:text-red-500
    text-zinc-900/80
}